/* jquery ui overrides
====================== */
.ui-icon-show {
    background-image: url(show.png) !important;
    background-position: 0 0;
}

.ui-icon-hide {
    background-image: url(hide.png) !important;
    background-position: 0 0;
}

.ui-widget-header:hover {
    cursor: move;
}

.ui-icon:hover {
    cursor: pointer;
}

.ui-sortable-helper {
    width: 20em !important;
}

.faceted-ui-dialog {
    border: 1px solid #ccc;
    border-radius: 10px;
    box-shadow: 5px 5px 5px #888;
    width: 90% !important;
    max-width: 50em;
}

.faceted-ui-dialog select {
    width: 100%;
}

.formHelp {
    display: block;
}

/* Common
========= */
.faceted-results {
    border: 1px dotted #ccc;
}

dd {
    margin-left: 0;
    padding-bottom: 1em;
}

table.listing {
    width: 100%;
}

/* Loading
========== */
.faceted_loading {
    text-align: center;
}

.faceted-loading-overlay {
    background: none !important;
    border: none !important;
    width: 120px !important;
}

.faceted-loading-overlay .ui-dialog-titlebar {
    display: none !important;
}

.faceted-loading-overlay .ui-resizable-handle {
    display: none !important;
}

/* Widgets
========== */
#content .faceted-widget fieldset,
.faceted-widget fieldset {
    border: none;
    padding: 0 1em 1em;
    margin: 1em 0;
}

.faceted-widget {
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 3px 3px 3px #888;
    margin: 2em 0.5em;
}

.faceted-widget legend {
    width: 100%;
}

.ui-widget-header {
    margin-bottom: 1em;
    text-align: left;
    padding: 1em;
    font-size: 120%;
    font-weight: lighter;
    background: #9acee6;
    border: none;
}

.ui-draggable .ui-dialog-titlebar {
    background: transparent;
    border-bottom: 1px dotted #e6e6e6;
    border-radius: initial;
}

.faceted-widget .ui-widget-header {
    margin-top: -0.9em;
    margin-left: -0.9em;
    margin-right: -0.9em;
    border-top-right-radius: 0.5em;
    border-top-left-radius: 0.5em;
}

.faceted-section-tabs ul.formTabs,
.faceted-widget-edit ul.formTabs {
    background-color: #e6e6e6;
    border: 0.3em solid #e6e6e6;
    display: block;
    border-style: solid solid none solid;
    margin-left: 0;
    border-top-right-radius: 0.5em;
    border-top-left-radius: 0.5em;
    font-size: 125%;
    font-weight: lighter;
}

.faceted-section-tabs li.formTab,
.faceted-widget-edit li.formTab {
    display: inline;
    margin-right: 0.25em;
}

.faceted-widget-edit li.formTab.ui-state-active {
    background: #ffffff !important;
    border: 1px solid #c5c5c5;
    border-bottom-width: 0;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    margin-bottom: 0;
}

.faceted-section-tabs li.formTab a,
.faceted-widget-edit li.formTab a {
    display: inline-block;
}

.faceted-section-tabs li.formTab a,
.faceted-widget-edit li.formTab a {
    line-height: 2em;
    padding: 0 0.75em;
    text-decoration: none;
    border: none;
    margin: 0;
}

.faceted-section-tabs li.formTab a.selected,
.faceted-section-tabs li.formTab a:hover,
.faceted-widget-edit li.formTab a.current,
.faceted-widget-edit li.formTab a:hover {
    background: #ffffff !important;
    color: Black;
}

.faceted-widget-edit li.formTab.ui-state-active a,
.faceted-widget-edit li.formTab.ui-state-active a:hover {
    background: transparent !important;
    color: Black;
}

.faceted-widget-edit .panes {
    padding: 1em;
}

.faceted-widget-hidden div.ui-widget-header {
    background: #e6e6e6 !important;
}

.faceted-widget-hidden fieldset {
    border: 1px solid #ccc;
}

#faceted-edit-widgets-ajax {
    text-align: right;
    clear: both;
    overflow: hidden;
    margin-top: 1em;
}

#faceted-edit-widgets-ajax input {
    margin-right: 0.5em;
}

#faceted-page-title {
    padding-right: 0;
}

.faceted-ajax {
    cursor: pointer;
    float: right;
}

/* Areas
*/
.faceted-area.center-content-area {
    /* margin: 0 25em 0 25em; */
}

/* Columns
========== */
.faceted-column {
    border-color: #ccc;
    border-style: dotted;
    border-width: 1px;
}

.faceted-empty-column {
    font-size: 200%;
    opacity: 0.8;
    color: #9acee6;
    text-align: center;
    padding-bottom: 0.5em;
}

/* Top
*/
#faceted-top-column::before {
    content: "Top widgets";
    text-align: center;
    width: 100%;
    color: #9acee6;
    float: right;
    font-weight: lighter;
    margin-top: 1em;
    height: 0;
}

