/* Tabs
*/
.faceted-section-tabs {
    margin-top: 1em;
    padding: 0;
}

.faceted-section-tabs .ui-tabs-nav {
    padding: 0;
}

.faceted-section-tabs ul li a {
    border-bottom: 0 !important;
    cursor: pointer;
    color: #205c90;
}

.faceted-section-tabs .ui-state-highlight {
    border: none !important;
    display: inline;
}

.faceted-section-tabs ul {
    list-style-type: none !important;
    margin: 0 !important;
}

/* Buttons
========== */

.faceted-add-button {
    padding: 1em;
}

.faceted-add-button span {
    border-color: #007bb1;
    border-style: dotted;
    border-width: 1px;
}

#main-container .faceted-add-button span {
    padding: 8.5px;
}

/* Errors
=========  */
#faceted-edit-widgets .faceted-widget-error {
    display: block;
}

#faceted-edit-widgets .faceted-widget-error div {
    text-align: center;
    color: red;
}

#faceted-edit-widgets .faceted-widget-error dd {
    margin-left: 2em;
    padding-bottom: 0;
}

.faceted-ui-dialog .ui-dialog-titlebar-close {
    right: 1em;
    background: transparent;
    border: none;
}

#faceted-portal-status-message {
    position: fixed;
    bottom: 0;
    left: 50%;
    z-index: 3;
    background: #aee2f2;
    border: 1px solid #ccc;
    border-bottom: none;
    text-align: center !important;
    width: 480px !important;
    margin-left: -240px;
    margin-bottom: 0;
    border-radius: 0;
    font-family: sans-serif;
    font-size: 120%;
    padding: 2rem;
}

#faceted-portal-status-message.success {
    background: #c9eab1;
}

#faceted-portal-status-message.error {
    background: #f5c1c1;
}

#faceted-portal-status-message.warning {
    background: #f3e2ab;
}

#faceted-portal-status-message .ui-icon {
    margin: 0 !important;
}

#faceted-portal-status-message dt {
    display: none;
}

#faceted-portal-status-message dd {
    padding-bottom: 0;
}
